<template>
  <div v-show="loader" id="preloader"></div>
  <div class="container content">
    <div v-show="this.user.verified == 0" class="row">
      <div class="col-12">
        <div class="alert alert-danger alert-dismissible rounded-10">
          <h5>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="25"
              height="25"
              fill="currentColor"
              class="bi bi-info-circle"
              viewBox="0 0 16 16"
            >
              <path
                d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"
              />
              <path
                d="m8.93 6.588-2.29.287-.082.38.45.083c.294.07.352.176.288.469l-.738 3.468c-.194.897.105 1.319.808 1.319.545 0 1.178-.252 1.465-.598l.088-.416c-.2.176-.492.246-.686.246-.275 0-.375-.193-.304-.533L8.93 6.588zM9 4.5a1 1 0 1 1-2 0 1 1 0 0 1 2 0z"
              />
            </svg>
            Fitur Hutang Belum Aktif!
          </h5>
          Silahkan ajukan verifikasi untuk centang biru agar bisa mengaktifkan
          fitur hutang dan validasi data diri.
          <a
            class="badge badge-success"
            :href="
              'https://api.whatsapp.com/send?phone=6285847801933&text=Nama : ' +
              this.user.name +
              '%0AUsername : ' +
              this.user.username +
              '%0A%0A%0AHalo Yoga, Saya ingin mengajukan verifikasi diri.'
            "
            >Ajukan</a
          >
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-12">
        <div class="card bg-biru shadow-lg rounded-10 border-0">
          <div class="card-body">
            <div class="row">
              <div class="col-12">
                <div v-if="this.user.profile != null">
                  <img
                    v-if="this.user.profile.slice(0, 4) === 'http'"
                    :src="this.user.profile"
                    :alt="this.user.profile"
                    width="150"
                    height="150"
                    class="rounded-circle border border-light"
                    data-toggle="modal"
                    data-target="#changeProfile"
                  />
                  <img
                    v-else
                    :src="
                      ipAdress + 'lte/dist/img/profile/' + this.user.profile
                    "
                    :alt="this.user.profile"
                    width="150"
                    height="150"
                    class="rounded-circle border border-light"
                    data-toggle="modal"
                    data-target="#changeProfile"
                  />
                </div>
                <div v-else>
                  <img
                    :src="ipAdress + 'lte/dist/img/profile/noprofil.jpg'"
                    :alt="this.user.profile"
                    width="150"
                    height="150"
                    class="rounded-circle border border-light"
                    data-toggle="modal"
                    data-target="#changeProfile"
                  />
                </div>

                <!-- Modal -->
                <div
                  class="modal fade"
                  id="changeProfile"
                  data-backdrop="static"
                  data-keyboard="false"
                  tabindex="-1"
                  aria-labelledby="changeProfileLabel"
                  aria-hidden="true"
                >
                  <div class="modal-dialog">
                    <div class="modal-content">
                      <div class="modal-header">
                        <h5 class="modal-title" id="changeProfileLabel">
                          Ganti Profil
                        </h5>
                        <button
                          @click="modalChangeProfileOnClose"
                          type="button"
                          class="close"
                          data-dismiss="modal"
                          aria-label="Close"
                          id="close"
                        >
                          <span aria-hidden="true">&times;</span>
                        </button>
                      </div>
                      <div class="modal-body">
                        <div
                          v-if="
                            this.user.profile != null &&
                            this.profileOnChange == null
                          "
                        >
                          <img
                            :src="
                              ipAdress +
                              'lte/dist/img/profile/' +
                              this.user.profile
                            "
                            :alt="this.user.profile"
                            width="300"
                            class="border border-light"
                            data-toggle="modal"
                            data-target="#changeProfile"
                          />
                        </div>
                        <div
                          v-if="
                            this.user.profile == null &&
                            this.profileOnChange == null
                          "
                        >
                          <img
                            :src="
                              ipAdress + 'lte/dist/img/profile/noprofil.jpg'
                            "
                            :alt="this.user.profile"
                            width="300"
                            class="border border-light"
                            data-toggle="modal"
                            data-target="#changeProfile"
                          />
                        </div>
                        <div v-if="this.profileOnChange">
                          <img
                            :src="this.profileOnChange"
                            :alt="this.user.profile"
                            width="300"
                            class="border border-light"
                            data-toggle="modal"
                            data-target="#changeProfile"
                          />
                        </div>
                        <form method="post" enctype="multipart/form-data">
                          <div class="custom-file mt-3">
                            <input
                              accept="image/*"
                              required=""
                              name="photoProfile"
                              type="file"
                              class="custom-file-input"
                              id="photoProfile"
                              @change="onchangeProfile"
                            />
                            <label
                              v-if="profileOnChangeName"
                              class="custom-file-label text-left"
                              for="photoProfile"
                              >{{ this.profileOnChangeName }}</label
                            >
                            <label
                              v-else
                              class="custom-file-label text-left"
                              for="photoProfile"
                              >{{ this.user.profile }}</label
                            >
                          </div>
                        </form>
                      </div>
                      <div class="modal-footer">
                        <button
                          @click="onUploadProfile"
                          type="button"
                          class="btn btn-primary"
                        >
                          Simpan
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="card-footer bg-light">
            <h5 class="mb-0">
              {{ this.user.name }}
              <span v-show="this.user.verified == '1'"
                ><svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  fill="currentColor"
                  class="bi bi-patch-check-fill text-primary"
                  viewBox="0 0 16 16"
                >
                  <path
                    d="M10.067.87a2.89 2.89 0 0 0-4.134 0l-.622.638-.89-.011a2.89 2.89 0 0 0-2.924 2.924l.01.89-.636.622a2.89 2.89 0 0 0 0 4.134l.637.622-.011.89a2.89 2.89 0 0 0 2.924 2.924l.89-.01.622.636a2.89 2.89 0 0 0 4.134 0l.622-.637.89.011a2.89 2.89 0 0 0 2.924-2.924l-.01-.89.636-.622a2.89 2.89 0 0 0 0-4.134l-.637-.622.011-.89a2.89 2.89 0 0 0-2.924-2.924l-.89.01-.622-.636zm.287 5.984-3 3a.5.5 0 0 1-.708 0l-1.5-1.5a.5.5 0 1 1 .708-.708L7 8.793l2.646-2.647a.5.5 0 0 1 .708.708z"
                  /></svg
              ></span>
            </h5>
            <small v-if="this.jtrx >= 10000" class="m-0 text-success"
              >Platinum Member</small
            >
            <small v-else-if="this.jtrx >= 1000" class="m-0 text-warning"
              >Gold Member</small
            >
            <small v-else-if="this.jtrx >= 0" class="m-0 text-primary"
              >Silver Member</small
            >
            <p class="text-muted p-0">( {{ this.user.username }} )</p>

            <div class="row">
              <div class="col-12">
                <button
                  type="button"
                  @click="clearSessionUser()"
                  class="btn btn-lg btn-block btn-primary rounded-5"
                >
                  Logout
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Swal from "sweetalert2";
import axios from "axios";

export default {
  name: "CusProfileView",
  props: ["ipAdress", "transaksi", "dataHutang"],
  data() {
    return {
      photoProfile: null,
      user: JSON.parse(localStorage.getItem("SessionUser")),
      profileOnChange: null,
      profileOnChangeName: null,
      loader: false,
      jtrx: Object.values(this.transaksi.semua_mutasi_sebelumnya).filter(
        (trx) => trx.status === "SUCCESS"
      ).length,
    };
  },
  methods: {
    reloadDataCus() {
      axios({
        method: "get",
        url:
          this.ipAdress +
          "api/get-data-cus/" +
          this.user.id +
          "?username=" +
          this.user.username,
      })
        .then(function (response) {
          //handle success
          localStorage.setItem(
            "SessionUser",
            JSON.stringify(response.data.data)
          );
          window.location.reload();
        })
        .catch(function (response) {
          //handle error
          console.log(response);
        });
    },
    modalChangeProfileOnClose: function () {
      this.profileOnChange = null;
      this.profileOnChangeName = null;
    },
    clearSessionUser() {
      localStorage.removeItem("SessionUser");
      window.location.replace("/login");
      Swal.fire({
        icon: "success",
        title: "Logout Berhasil",
        text: "Goodbye",
        showConfirmButton: false,
        timer: 1500,
      });
    },
    onchangeProfile: function (event) {
      this.photoProfile = event.target.files[0];
      this.profileOnChange = URL.createObjectURL(event.target.files[0]);
      this.profileOnChangeName = event.target.files[0].name;
    },
    onUploadProfile: function () {
      document.getElementById("close").click();
      this.loader = true;
      const formData = new FormData();
      formData.append("photoProfile", this.photoProfile);
      axios({
        method: "post",
        url:
          this.ipAdress +
          "api/customers/ganti-profile/" +
          this.user.id +
          "?username=" +
          this.user.username,
        data: formData,
        headers: { "Content-Type": "multipart/form-data" },
      })
        .then(function (response) {
          //handle success
          Swal.fire({
            icon: "success",
            title: "Berhasil",
            text: "Profil berhasil diperbaharui",
            showConfirmButton: false,
            timer: 1500,
          });
        })
        .catch(function (response) {
          //handle error
          console.log(response);
        })
        .finally(() => {
          this.reloadDataCus();
        });
    },
  },
};
</script>

<style>
html,
body {
  text-align: center;
}

body {
  background-color: #ffffff;
}

.rounded-2 {
  border-radius: 8px;
}

.rounded-5 {
  border-radius: 10px;
}

.rounded-10 {
  border-radius: 20px;
}

#navbottom {
  border-radius: 30px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.bg-biru {
  background-color: #1583dd;
}

.content {
  margin-top: 100px;
  margin-bottom: 130px;
}

.bg-putih {
  background-color: #ffffff;
}

#preloader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  overflow: visible;
  background: #ffffff url("@/assets/loader/96x96 (2).gif") no-repeat center
    center;
}
</style>